import { Component, Input, Output, EventEmitter } from '@angular/core';

@Component({
  selector: 'addPicture',
  templateUrl: './addPicture.component.html'
})
export class AddPictureComponent {

  @Output()
  delete: EventEmitter<string> = new EventEmitter<string>();
  
  @Input()
  picture: string = "";

  constructor() { }

  displayImage() {
    if(!this.picture) {
      return "";
    }
    return this.picture;
  }

  deletePicture() {
    this.delete.emit(this.picture);
  }
}



