import { Component, NgZone } from '@angular/core';
import { NavController, PopoverController, Platform } from 'ionic-angular';
import { BarcodeScanner } from '@ionic-native/barcode-scanner';
import { LoadingCtrlService } from '../../../share/loadingCtrlService';
import { BookService } from '../../../share/book.service';
import { ErrorService } from '../../../share/error.service';

@Component({
  selector: 'isbn-check-page',
  templateUrl: 'isbnCheck.component.html',
  styles: ['.list {margin-bottom:0;}']
})
export class IsbnCheckPage {
  isbn = "";
  popover;
  book: any = {};
  cordova = true;
  constructor(private _navCtrl: NavController, private _bookService: BookService, private popoverCtrl: PopoverController,
    private _loadingCtrlService: LoadingCtrlService, private _barcodeScanner: BarcodeScanner, private _platform: Platform,
    private _errorService: ErrorService, private _ngZone: NgZone) {
    if (!this._platform.is('cordova')) {
      this.cordova = false;
    }
  }

  onIsbnCheck() {
    if (this.isbn) {
      this._loadingCtrlService.presentLoading('Suche nach Büchern...');
      
      this._bookService.getBookByIsbn(this.isbn).subscribe(res => {
        this.book = res;
        this._loadingCtrlService.stopLoading();
        if (this.book.title) {
          this.redirectToAddItemPage(this.book);
        }
        else {
          this._errorService.displayErrorMessage('Kein Buch zur angegebenen ISBN: "' + this.isbn + '" gefunden!');
        }
        this.isbn = '';
      }, error => {
        this._loadingCtrlService.stopLoading();
        this._errorService.displayErrorMessage('Kein Buch zur angegebenen ISBN: ' + this.isbn + ' gefunden!');
      });
    }
  }

  presentBarocodeHelpPopover() {
    this.popover = this.popoverCtrl.create('BarcodeHelpComponent');
    this.popover.present();
  }

  onBarcodeScanner() {
    this._barcodeScanner.scan().then(succ => {
      this.isbn = succ.text;
      this.onIsbnCheck();
    })
      .catch(error => {
        
      });
  }

  onShowBarcodeHelp() {
    this.presentBarocodeHelpPopover();
  }

  redirectToAddItemPage(book) {
    this._ngZone.run(() => {
      if(book) {
        this._navCtrl.push('AddItemPage', {
          book: this.book
        });
      }
      else {
        this._navCtrl.push('AddItemPage'
      );
      }
    });
  }
}


