import { Component } from '@angular/core';
import { NavController, NavParams } from 'ionic-angular';
import { DatePicker } from '@ionic-native/date-picker';
import { LoginService } from '../../share/login.service';
import { BookService } from '../../share/book.service';
import { AddBook } from '../../model/addBook';
import { LoadingCtrlService } from '../../share/loadingCtrlService';
import { ToastCtrlService } from '../../share/toast.service';
import { ActionSheetCtrlService } from '../../share/actionSheetCtrl.service';
import { ErrorService } from '../../share/error.service';
import { Validators, FormGroup, FormBuilder } from '@angular/forms';

@Component({
  selector: 'addItem-page',
  templateUrl: 'addItem.component.html',
  styles: ['.pic { height : 40%;    display: flex;flex-direction: row;justify-content: center;align-items: center;}']
})
export class AddItemPage {

  pictures = [];
  showPics: boolean = false;
  showGroup: boolean = false;
  book: AddBook = <AddBook>{title:"",authors:[""],genre:[""],bookdescription:"",publishcompany:"",sites:"",isbn:"",language:"de",publishdate:"",
                            price:"",shipping:"",state:""};
  icon = 'add';
  private addBook: FormGroup;

  constructor(public navCtrl: NavController,private _bookService: BookService, private _navParams: NavParams,
    private _datePicker: DatePicker, private _loginService: LoginService,private _loadingCtrlService: LoadingCtrlService, 
    private _toastCtrlService: ToastCtrlService, private _actionSheetCtrlService: ActionSheetCtrlService,
    private _errorService: ErrorService, private _formBuilder: FormBuilder) {
      this.createForm();
  }

  ionViewWillEnter() {
    let book = this._navParams.get('book');
    if(book) {
      this.book = book;
    }
    
  }

  addItem() {
    this._loadingCtrlService.presentLoading("Buch wird hinzugefügt...");

    this._loginService.getToken().then(token => {
      this.book.pictures = []

      // Copy pictures Arrays here
      this.book.pictures = this.pictures.slice();

      this._bookService.addBook(this.book, token).subscribe(succ => {
        this._loadingCtrlService.stopLoading();
        this._toastCtrlService.createToast("Buch erfolgreich hinzugefügt");
        this.navCtrl.popToRoot();
      }, error => {
        this._loadingCtrlService.stopLoading();
        this._errorService.displayErrorMessage("Das Buch konnte nicht hinzugefügt werden");
      });
    })
      .catch(error => {
        this._loadingCtrlService.stopLoading();
        this._errorService.displayErrorMessage("Das Buch konnte nicht hinzugefügt werden");
      });
  }

  uploadPhoto() {
    this._actionSheetCtrlService.uploadPhoto().then(pic => {
      if (pic !== 'failed') {
        this.pictures.push(pic);
        this.showPics = true;
      }
    }).catch(err => {
      
    });
  }

  onDeletePicture(pic: string) {
    this.pictures = this.pictures.filter(el => el !== pic);
    if(this.pictures.length < 1) {
      this.showPics = false;
    }
  }

  createForm() {
    this.addBook = this._formBuilder.group({
      title: ['', [
        Validators.required
      ]
      ],
      authors: ['', [
        Validators.required
      ]
      ],
      isbn: ['', [
        Validators.required
      ]
      ],
      genre: ['', [
        Validators.required
      ]
      ],
      bookdescription: ['', [
        Validators.required
      ]
      ],
      publishcompany: ['', [
        Validators.required
      ]
      ],
      sites: ['', [
        Validators.required
      ]
      ],
      language: ['', [
        Validators.required
      ]
      ],
      publishdate: ['', [
        Validators.required
      ]
      ],
      price: ['', [
        Validators.required
      ]
      ],
      shipping: ['', [
        Validators.required
      ]
      ],
      state: ['', [
        Validators.required
      ]
      ]
    });

    console.log(this.addBook.controls['title'].value);
  }

  isBookValid() {
    if (this.book.title && this.book.authors && this.book.isbn && this.book.price && this.book.genre
      && this.book.shipping && this.book.state && this.pictures[0]) {
      return true;
    }
    return false;
  }

  showDatePicker() {
    this._datePicker.show({
      date: new Date(),
      mode: 'date',
      androidTheme: this._datePicker.ANDROID_THEMES.THEME_HOLO_DARK
    }).then(
      date => console.log('Got date: ', date),
      err => console.log('Error occurred while getting date: ', err)
      );
  }

  showOrHideDetails() {
    this.showGroup = !this.showGroup;
    this.displayIcon();
  }

  displayIcon() {
    if (this.showGroup) {
      this.icon = 'remove';
    } else {
      this.icon = 'add';
    }
  }

  displayImage(picture) {
    return picture;
  }
}


