import { Component } from '@angular/core';
import { NavController } from 'ionic-angular';
import { LoginService } from '../../share/login.service';
import { Adress } from '../../model/adress';


@Component({
    selector: 'myProfile-page',
    templateUrl: 'myProfile.component.html'
})
export class MyProfilePage {
    user;
    payments = [{ name: 'Marco Döll', iban: 'DE7392123192839' }, { name: 'Julian Fruth', iban: 'DE92019520002' }];
    addresses = [{ 'name': 'Marco Döll', 'street': 'Gartnerstraße 6', 'additive': 'a', 'plz': '90607', 'city': 'Nürnberg' }];

    googleText = 'Mit Google verbinden';
    googleVerified = false;

    mailText = 'E-Mail bestätigen'
    smsText = 'Per SMS bestätigen';
    smsConfirmed = false;

    constructor(private _navCtrl: NavController, private _loginService: LoginService) {
    }

    ionViewWillEnter() {
        this.checkVerifcations();
    }

    checkVerifcations() {
        this.user = this._loginService.getUser();

        this._loginService.getProviderData().forEach(data => {
            if (data.providerId === 'google.com') {
                this.setGoogleConnectedInformation();
            }
        });

        if (this.user.emailVerified) {
            this.mailText = "E-Mail bestätigt";
        }
    }

    linkGoogle() {
        if (!this.googleVerified) {
            this._loginService.linkUserWithGoogle().then(suc => {
                this.googleVerified = suc;
                if(this.googleVerified) {
                    this.setGoogleConnectedInformation();
                }
            }).catch(err => {
                
            });
        }
    }

    onPayment(account) {
        this._navCtrl.push('AddEditPaymentPage', { account: account });
    }

    onAddress(address: Adress) {
        this._navCtrl.push('AddEditAddressComponent', { address: address });
    }

    onEditProfile() {
        this._navCtrl.push('EditProfilePage');
    }

    sendMail() {
            this._loginService.sendMailVerification();
    }

    private setGoogleConnectedInformation() {
        this.googleVerified = true;
        this.googleText = 'Mit Google verbunden';
    }
}


