import { Component } from '@angular/core';
import { NavController} from 'ionic-angular';
import { SearchService } from '../../../share/search.service';

@Component({
  selector: 'search-genre-page',
  templateUrl: 'search-genre.component.html'
})
export class SearchGenrePage {

  items: string[];

  private initializeItems() {
    this.items = [
      'Krimi',
      'Roman',
      'Comedy',
      'Sachbücher',
      'Autobiographie',
      'Liebesroman',
      'Kinderbuecher',
      'Jugendbücher',
      'Historie',
      'Science Fiction',
      'Harry Potter 7',
      'Ratgeber',
      'Klassiker',
      'Comics',
    ];
  }

  constructor(private _navCtrl: NavController, private _searchService: SearchService) {
    this.initializeItems();
  }
  
  onGenrePage(item :string) {
    this._searchService.triggerFilterEvent(item);
    this._navCtrl.pop();
  }
}


