import { Component } from '@angular/core';
import { NavController } from 'ionic-angular';
import { Validators, FormBuilder, FormGroup } from '@angular/forms';
import { LoginService } from '../../../share/login.service';


@Component({
  selector: 'mail-login-page',
  templateUrl: 'mail-login.component.html'
})
export class MailLoginPage {
  private login: FormGroup;

  constructor(private _navCtrl: NavController, private _loginService: LoginService, private _formBuilder: FormBuilder) {
    this.createForm();
  }

  onRegister() {
    this._navCtrl.push('RegisterPage');
  }

  onLogin() {
    let mail = this.login.get('mail').value;
    let password = this.login.get('password').value;
    
    this._loginService.loginWithMail(mail,password)
  }

  createForm() {
    this.login = this._formBuilder.group({
      mail: ['', [
        Validators.required,
        Validators.minLength(4)
      ]
      ],
      password: ['', [
        Validators.required,
        Validators.minLength(4)
      ]
      ],
    });
  }
}


