import { Component } from '@angular/core';
import { DexieDatabaseService } from '../../share/dexieDatabase.service';
import { BookPreview } from '../../model/bookPreview';
import { BookService } from '../../share/book.service';
import { PictureService } from '../../share/picture.service';

@Component({
  selector: 'favorites-page',
  templateUrl: 'favorites.component.html'
})
export class FavoritesPage {

  private seller = {
    accountName: 'King Döll',
    profilePicture: 'https://www.fupa.net/fupa/images/photo/big/marco-doell-954161_1.jpg'
  };
  items: BookPreview[] = [];

  ids: string[] = [];

  constructor(private _databaseService: DexieDatabaseService, private _bookService: BookService, private _pictureService: PictureService) {
  }

  ionViewDidLoad() {
    this.initFavorites();
  }

  initFavorites() {
    this._databaseService.getAllFavorites().then(succ => {
      succ.forEach(elem => {
        this.ids.push(elem.id);
      });

      this.loadBooks();
    })
      .catch(error => {

      });
  }

  loadBooks() {
    this._bookService.getBooksFromIds(0,50000,this.ids).subscribe(res => {
        let books: BookPreview[] = res;

        books.forEach(element => {
          this.items.push(element);
        });

        // Bilder für alle items holen
        this._pictureService.loadThumbnails(this.items);
        
    }, error => {
      //alert("Fehler beim laden der Bücher");
    });
  }

  doRefresh(refresher) {
    this.initOnlyForTest(); 
    setTimeout(() => {
      refresher.complete();
    }, 1500);
  }

  initOnlyForTest() {
    this.ids = [];
    this.items = [];
    this.initFavorites();
  }
}


