import { Injectable } from "@angular/core";
import { Platform } from "ionic-angular";

import { LoadingCtrlService } from "./loadingCtrlService";
import { GooglePlus } from '@ionic-native/google-plus';
import firebase from 'firebase';
import { ErrorService } from "./error.service";
import { ToastCtrlService } from "./toast.service";


@Injectable()
export class LoginService {

    user: firebase.User = null;
    constructor(public googleplus: GooglePlus, private _loadingCtrlService: LoadingCtrlService, private _platform: Platform
        , private _errorCodeService: ErrorService, private _toastCtrlService: ToastCtrlService) {
        firebase.auth().onAuthStateChanged(user => {
            if (user) {
                this.user = user;
            } else {
                this.user = null;
            }
        });
    }

    sendMailVerification(user?) {
        if (!this.user.emailVerified) {
            this.user.sendEmailVerification().then(succ => {
                this._toastCtrlService.createToast("Bestätigungsmail versandt!");
            }).catch(error => {
                this._errorCodeService.displayErrorMessage("Bestätigungsmail konnte nicht versendet werden!");
            });
        }
    }

    getToken(): Promise<any> {
        if (this.user) {
            return this.user.getIdToken();
        }
    }

    loginWithMail(mail, password) {
        this._loadingCtrlService.presentLoading("Einloggen...");

        firebase.auth().signInWithEmailAndPassword(mail, password).then(succ => {
            this._loadingCtrlService.stopLoading();
        }).
            catch(error => {
                this._loadingCtrlService.stopLoading();
                this._errorCodeService.displayErrorMessageFromCode(error.code);
            });
    }

    getUser(): firebase.User {
        return this.user;
    }

    getUserForLogin() {
        return firebase.auth();
    }

    getProviderData() {
        return this.user.providerData;
    }


    loginWithGoogle() {
        // Native Google Login
        if (this._platform.is('cordova')) {
            this._loadingCtrlService.presentLoading("Einloggen...");

            this.loginWithGoogleNative().then(res => {
                firebase.auth().signInWithCredential(firebase.auth.GoogleAuthProvider.credential(res.idToken))
                    .then(suc => {
                        this._loadingCtrlService.stopLoading();

                    }).catch(error => {
                        this.handleError(error);
                        this._errorCodeService.displayErrorMessageFromCode(error.code);
                    });
            }).catch(error => {
                this.handleError(error);
                this._errorCodeService.displayErrorMessageFromCode(error.code);
            });
        }

        // Web Google Login
        else {
            this.loginWithGoogleWeb().then(succ => {
                this._loadingCtrlService.stopLoading();

            }).catch(error => {
                this.handleError('error in Google-Sign-Up ' + error);
                this._errorCodeService.displayErrorMessageFromCode(error.code);
            });
        }
    }

    linkUserWithGoogle(): Promise<boolean> {
        let provider = new firebase.auth.GoogleAuthProvider();
        var that = this;

        // Native Google Login
        if (this._platform.is('cordova')) {
            this._loadingCtrlService.presentLoading("Einloggen...");

            return this.loginWithGoogleNative().then(res => {
                return this.user.linkWithCredential(firebase.auth.GoogleAuthProvider.credential(res.idToken))
                    .then(suc => {
                        return handleSuccessfullLogin();
                    }).catch(error => {
                        this._errorCodeService.displayErrorMessageFromCode(error.code);
                        return this.handleError('Firebase Login with Google-Credentials not successfull' + error);
                    });
            }).catch(error => {
                this._errorCodeService.displayErrorMessageFromCode(error.code);
                return this.handleError('Login war nicht erfolgreich - bitte erneut probieren');
            });
        }
        // Web Google Login
        else {
            return this.user.linkWithPopup(provider).then(res => {
                return handleSuccessfullLogin();
            })
                .catch(error => {
                    this._errorCodeService.displayErrorMessageFromCode(error.code);
                    return this.handleError(error);
                });
        }

        function handleSuccessfullLogin() {
            that._loadingCtrlService.stopLoading();
            that._toastCtrlService.createToast('Erfolgreich mit deinem Google-Konto verbunden!');
            return true;
        }
    }

    registerUser(mail, password) {
        this._loadingCtrlService.presentLoading("Registrieren...");

        firebase.auth().createUserWithEmailAndPassword(mail, password).then(user => {
            // User setzen und updaten (Default Profilbild und Mail als DisplayName)
            this.user = user;
            this.updateUser(user.email, "https://www.obgyn.pitt.edu/sites/default/files/default_images/default-person.jpg");
            this.sendMailVerification();
            this._loadingCtrlService.stopLoading();
        })
            .catch(error => {
                this._loadingCtrlService.stopLoading();
                this._errorCodeService.displayErrorMessageFromCode(error.code);
            });
    }

    logOut() {
        firebase.auth().signOut();
        this.googleplus.logout();
    }

    updateUser(name :string, photo : string) {
        if (!name) {
            name = this.user.displayName;
        }
        if (!photo) {
            photo = this.user.photoURL;
        }

        this.user.updateProfile({
            displayName: name,
            photoURL: photo
        }).then(suc => {
            // Update erfolgreich
        })
            .catch(error => {
                this._errorCodeService.displayErrorMessage("Profil Update war nicht erfolgreich!");
            });
    }

    private loginWithGoogleWeb() {
        var provider = new firebase.auth.GoogleAuthProvider();

        return firebase.auth().signInWithPopup(provider);
    }

    private loginWithGoogleNative() {
        return this.googleplus.login({
            'webClientId': '269041530494-lb02teh79edmiacl08baq4m3b28f0lh1.apps.googleusercontent.com',
            'offline': true
        });
    }

    private handleError(message) {
        this._loadingCtrlService.stopLoading();
        return false;
    }
}


