import { Component } from '@angular/core';
import { NavParams } from 'ionic-angular';
import { Payment } from '../../../model/account';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';

@Component({
    selector: 'addEditPayment-page',
    templateUrl: 'addEditPayment.component.html'
})
export class AddEditPaymentPage {
    buttonTitle = 'Speichern';
    title = 'Konto bearbeiten';
    private payment: FormGroup
    constructor(private _navParams: NavParams, private _formBuilder: FormBuilder) {
        this.createForm();
    }

    ionViewWillEnter() {
        var acc = <Payment> this._navParams.get('account');

        if(acc) {
            this.payment.controls['name'].setValue(acc.name);
            this.payment.controls['iban'].setValue(acc.iban);
        }

        // Keine Payment-Methode gefunden
        if(!this.payment.controls['name'].value) {
            this.buttonTitle = 'Hinzufügen';
            this.title = 'Konto hinzufügen';
        }
    }

    addItem() {
        alert("implement todo");
    }

    private createForm() {
        this.payment = this._formBuilder.group({
            name: ['', [
                Validators.required
            ]
            ],
            iban: ['', [
                Validators.required
            ]
            ]
        });
    }
}




