import { Injectable } from "@angular/core";
import { Http, Headers } from "@angular/http";
import { Observable } from "rxjs/Observable";
import { environment } from "../environments/environments";
import { BookDetail } from "../model/bookDetail";
import 'rxjs/Rx';

@Injectable()
export class BookService {
    constructor(private _http: Http) {
    }

    getBookByIsbn(isbn: string): Observable<any> {
        return this._http.get(environment.apiUrl + 'bookinfo/' + isbn).map(res => {
            return res.json();
        }).catch(error => {
            throw new Error(error);
        });
    }

    addBook(book, token): Observable<any> {
        let myHeaders = new Headers();
        myHeaders.append('token', token);
        myHeaders.append('Content-Type', 'application/json');

        var jsonBook = JSON.stringify(book);

        return this._http.post(environment.apiUrl + 'books/sale', jsonBook, { headers: myHeaders }).map(res => {
            return res;
        })
            .catch(error => {
                throw new Error(error);
            });
    }

    getBooks(from, to): Observable<any> {
        let myHeaders = new Headers();
        myHeaders.append('Content-Type', 'application/json');

        let query = `{  
            "from":"${from}",
            "to":${to}
         }`;

        return this._http.post(environment.apiUrl + 'books/query', query, { headers: myHeaders }).map(res => {
            return res.json();
        })
            .catch(error => {
                throw new Error("Error trying to get Books");
            });
    }

    getBooksFromIds(from, to, ids): Observable<any> {
        let myHeaders = new Headers();
        myHeaders.append('Content-Type', 'application/json');

        let test = <any>{};
        test.filters = []
        test.filters[0] = {key:"id",values:ids}
        
        let query = `{  
            "from":"${from}",
            "to":"${to}",
            "filters":${JSON.stringify(test.filters)}
         }`;

        return this._http.post(environment.apiUrl + 'books/query', query, { headers: myHeaders }).map(res => {
            return res.json();
        })
            .catch(error => {
                throw new Error("Error trying to get Books");
            });
    }

    getBookDetail(id: string): Observable<BookDetail> {
        return this._http.get(environment.apiUrl + 'books/detail/' + id).map(res => {
            return res.json();
        })
            .catch(error => {
                throw new Error("Could not get detailed book from Id:" + id);
            });
    }
}


