import Dexie from 'dexie';

export class MyAppDatabase extends Dexie {
    favorites: Dexie.Table<IFavorite, string>;

    constructor() {
        super("MyAppDatabase");

        var db = this;

        this.version(1).stores({
            favorites: "id&"
        });

    }
}

interface IFavorite {
    id: string;
}


