import { Component } from '@angular/core';
import { NavParams } from 'ionic-angular';
import { Message } from '../../../model/message';

@Component({
  selector: 'messages-detail-page',
  templateUrl: 'messages-detail.component.html',
  styles:['.container {width:75%; margin-left:2.5rem;}']
})
export class MessagesDetailPage {

  private receiver  = {accountName:'King Döll', 
  profilePicture:'https://www.fupa.net/fupa/images/photo/big/marco-doell-954161_1.jpg'};
  
  private sender  = {accountName:'King Döll', 
  profilePicture:'https://www.fupa.net/fupa/images/photo/big/marco-doell-954161_1.jpg'};

  private message :Message = {title:'Entdecke neue Abenteuer', text:'Hallo XYZ ....',sender:this.sender,receiver:this.receiver }

  constructor(private navParams :NavParams) {
    this.message = this.navParams.get('message');
    this.sender = this.message.sender;
    this.receiver = this.message.receiver;
  }
}


