import { NgModule } from '@angular/core';
import {IonicPageModule} from 'ionic-angular';
import { MyApp } from './app.component';



@NgModule({
  declarations: [
    MyApp
  ],
  imports: [
    IonicPageModule.forChild(MyApp)
  ],
  entryComponents: [
    MyApp
  ]
})
export class MyAppModule {}


