import { Component } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';

import { LoginService } from '../../share/login.service';
import { ErrorService } from '../../share/error.service';

@Component({
  selector: 'register-page',
  templateUrl: 'register.component.html'
})
export class RegisterPage {
  registerForm: FormGroup;

  constructor(private _formBuilder: FormBuilder, private _loginService: LoginService,
              private _errorService: ErrorService) {
    this.createForm();
  }

  onRegister() {
    var mail = this.registerForm.get('mail').value;
    var password = this.registerForm.get('password').value;
    var passwordVerification = this.registerForm.get('passwordSecond').value;

    // Falls Passwörter nicht uebereinstimmen
    if(password !== passwordVerification) {
      this._errorService.displayErrorMessage('Die beiden Passwörter stimmen nicht überein.');
      return;
    }

    this._loginService.registerUser(mail,password);
  }

  private createForm() {
    this.registerForm = this._formBuilder.group({
      mail: ['', [
        Validators.required,
        Validators.minLength(4)
      ]
      ],
      password: ['', [
        Validators.required,
        Validators.minLength(4)
      ]
      ],
      passwordSecond: ['', [
        Validators.required,
        Validators.minLength(4)
      ]
      ],
    });
  }



}


