import { Component } from '@angular/core';
import { NavController, NavParams } from 'ionic-angular';

@Component({
    selector: 'isbn-book-item-page',
    templateUrl: 'isbnBookItem.component.html',
    styles: ['']
})
export class IsbnBookItemPage {
    book;
    constructor(private _navCtrl: NavController, private _navParams: NavParams) {
        this.book = this._navParams.get('book');
    }

    redirectToAddItemPage() {
        this._navCtrl.push('AddItemPage', {
            book: this.book
        });
    }

    onItemClick(event: any) {
        this.redirectToAddItemPage();
    }
}


