import { Component, AfterViewInit, NgZone } from '@angular/core';
import { NavController } from 'ionic-angular';
import { StatusBar } from '@ionic-native/status-bar';

import { LoginService } from '../../share/login.service';
import { IonicPage } from 'ionic-angular/navigation/ionic-page';


@IonicPage({
  name: 'LoginPage',
  segment: 'sTest'
})
@Component({
  selector: 'login-page',
  templateUrl: 'login.component.html'
})
export class LoginPage {

  constructor(private _navCtrl: NavController, private _loginService: LoginService, private _zone: NgZone) {
  }
  ionViewDidEnter() {
    this.checkLogin();
  }

  googleLogin() {
    this._loginService.loginWithGoogle();
  }

  private goToMainpage() {
    this._zone.run(() => {
      this._navCtrl.setRoot('TabsPage');
    });
  }

  checkLogin() {
    var unsubscribe = this._loginService.getUserForLogin().onAuthStateChanged(user => {
      if (user) {
        this.goToMainpage();
        unsubscribe();
      }
    }, error => {
      console.log(error);
    });
  }

  onLoginWithMail() {
    this._navCtrl.push('MailLoginPage');
  }
}


