import { Component, Input } from '@angular/core';
import { NavController } from 'ionic-angular';

import { Message } from '../../../model/message';


@Component({
  selector: 'messages-item',
  templateUrl: './messages-item.component.html',
  styles: ['.item {border-bottom: 1px solid lightgray !important;}']
})
export class MessagesItem {

@Input() sender  = {accountName:'King Döll', 
profilePicture:'https://www.fupa.net/fupa/images/photo/big/marco-doell-954161_1.jpg'};

@Input() receiver = {accountName:'King Döll', 
profilePicture:'https://www.fupa.net/fupa/images/photo/big/marco-doell-954161_1.jpg'};

@Input() message :Message = {title:'Entdecke neue Abenteuer', text:'Hallo XYZ ....',sender:this.sender,receiver:this.receiver }
  constructor(private navCtrl :NavController) {
  }

  onMessage() {
    this.navCtrl.push('MessagesDetailPage',{message:this.message});
  }


}


