import { Component } from '@angular/core';
import { NavController, App } from 'ionic-angular';
import { LoginService } from '../../share/login.service';

@Component({
  selector: 'account-page',
  templateUrl: 'account.component.html'
})
export class AccountPage {
  user;

  constructor(private _navCtrl: NavController, private app: App, private _loginService: LoginService) {
  }

  ionViewWillEnter() {
    this.getUser();
  }

  private getUser() {
    this.user = this._loginService.getUser();
  }

  onMyProfile() {
    this._navCtrl.push('MyProfilePage',{user:this.user},{animate:true,animation:'transition',direction:'forward'});
  }

  onFavorites() {
    this._navCtrl.push('FavoritesPage',{},{animate:true,animation:'transition',direction:'forward'});
  }

  onLogout() {
    this._loginService.logOut();
    this.app.getRootNav().setRoot('LoginPage');
  }
}


