import { Component } from '@angular/core';
import { ModalController } from 'ionic-angular';

@Component({
  selector: 'tabs-page',
  templateUrl: 'tabs.component.html'
})
export class TabsPage {
  public tab1: any = 'BooksBrowsingPage';
  public tab3: any = 'IsbnCheckPage';
  public tab4: any = 'MessagesPage';
  public tab5: any = 'AccountPage';

  tab1Title = "Entdecken";
  tab3Title = "Verkaufen";
  tab4Title = "Nachrichten";
  tab5Title = "Account";

constructor(public modalCtrl: ModalController) {
  }

  addBook() {
    let modal = this.modalCtrl.create('IsbnCheckPage');
    modal.present();
    
  }
}


