import { Injectable } from "@angular/core";

import { Dexie } from 'dexie';
import { MyAppDatabase } from "../model/dexieDatabase/FavoritesDatabase";

@Injectable()
export class DexieDatabaseService {
    private db: MyAppDatabase;
    constructor() {
        this.db = new MyAppDatabase();
    }

    getAllFavorites() {
        return this.db.table("favorites").toArray();
    }

    addFavorite(id: string) {
        this.db.table("favorites").add({id:id});
    }

    isFavorite(id:string) {
        return this.db.table("favorites").get(id);
    }

    removeFavorite(id: string) {
        this.db.table("favorites").delete(id);
    }
}


