import { Component, Input, AfterViewInit, NgZone } from '@angular/core';
import { BookPreview } from '../../../model/bookPreview';
import { DexieDatabaseService } from '../../../share/dexieDatabase.service';

@Component({
  selector: 'book-item',
  templateUrl: 'book-item.component.html',
  styles: ['.item { padding-left: 0;}']
})
export class BookItem implements AfterViewInit {
    favoriteIcon = "heart-outline";

    @Input() item :BookPreview = <BookPreview>{};
    @Input() seller = {accountName:'King Döll12345', profilePicture:'https://www.fupa.net/fupa/images/photo/big/marco-doell-954161_1.jpg'};
  
    constructor(private _databaseService: DexieDatabaseService, private _zone: NgZone) {
      
    }

    ngAfterViewInit() {
      this.isItemFavorite();
    }

    isItemFavorite() {
      this._databaseService.isFavorite(this.item.id).then(succ => {
        if(succ) {
          this.favoriteIcon = "heart";
        }
      }).catch(error => {

      });
    }

    onSetItemFavorite(item: BookPreview) {
      event.stopPropagation();
      this._zone.run(() => {
        if(this.favoriteIcon === "heart-outline") {
          this.favoriteIcon = "heart";
          this._databaseService.addFavorite(item.id);
        }
        else {
          this.favoriteIcon = "heart-outline";
          this._databaseService.removeFavorite(item.id);
        }
      });
    }

    displayThumbNail() {
      if(this.item.pictureThumbnail) {
        return this.item.pictureThumbnail;
      }
      else return '';
    }
}


