import { Component } from '@angular/core';
import { NavParams } from 'ionic-angular';
import { Adress } from '../../../model/adress';
import { Validators, FormGroup, FormBuilder } from '@angular/forms';

@Component({
    selector: 'addEditAddress-page',
    templateUrl: 'addEditAddress.component.html'
})
export class AddEditAddressComponent {
    title = 'Adresse bearbeiten';
    buttonTitle = 'Speichern';
    private addressForm: FormGroup;
    constructor(private _navParams: NavParams, private _formBuilder: FormBuilder) {
        this.createForm();
    }

    ionViewWillEnter() {
        var addr = <Adress> this._navParams.get('address');

        if (addr) {
            this.addressForm.controls['name'].setValue(addr.name);
            this.addressForm.controls['street'].setValue(addr.street);
            this.addressForm.controls['additive'].setValue(addr.additive);
            this.addressForm.controls['plz'].setValue(addr.plz);
            this.addressForm.controls['city'].setValue(addr.city);
        }

        // Keine Adresse gefunden
        if (!this.addressForm.controls['name'].value) {
            this.buttonTitle = 'Hinzufügen';
            this.title = 'Adresse hinzufügen'
        }
    }

    addItem() {
        alert("Speichern implement Todo!");
    }

    private createForm() {
        this.addressForm = this._formBuilder.group({
            name: ['', [
                Validators.required
            ]
            ],
            street: ['', [
                Validators.required
            ]
            ],
            additive: ['', [
                Validators.required
            ]
            ],
            plz: ['', [
                Validators.required
            ]
            ],
            city: ['', [
                Validators.required
            ]
            ]
        });
    }
}


