import { Injectable } from "@angular/core";
import { CameraOptions, Camera } from "@ionic-native/camera";

@Injectable()
export class CameraService {


    private libraryOptions: CameraOptions = {
        quality: 40,
        targetHeight:600,
        targetWidth:600,
        destinationType: this._camera.DestinationType.DATA_URL,
        encodingType: this._camera.EncodingType.JPEG,
        mediaType: this._camera.MediaType.PICTURE,
        sourceType: this._camera.PictureSourceType.PHOTOLIBRARY
    }

    private cameraOptions: CameraOptions = {
        quality: 40,
        targetHeight:600,
        targetWidth:600,
        destinationType: this._camera.DestinationType.DATA_URL,
        encodingType: this._camera.EncodingType.JPEG,
        mediaType: this._camera.MediaType.PICTURE,
        sourceType: this._camera.PictureSourceType.CAMERA,
    }

    constructor(private _camera: Camera) {
    }

    public takePic(optionsName: string): Promise<any> {
        let options: CameraOptions = this.getOptions(optionsName);

        return this._camera.getPicture(options).then(imageData => {
            return 'data:image/jpeg;base64,' + imageData;
            
        }, (err) => {
            return 'failed';
        });
    }

    private getOptions(optionsName): CameraOptions {
        if (optionsName === 'library') {
            return this.libraryOptions;
        }
        return this.cameraOptions;
    }
}


