import { Component, NgZone } from '@angular/core';
import { NavController, InfiniteScroll, ScrollEvent } from 'ionic-angular';
import { BookService } from '../../share/book.service';
import { BookPreview } from '../../model/bookPreview';
import { PictureService } from '../../share/picture.service';
import { ToastCtrlService } from '../../share/toast.service';
import { ErrorService } from '../../share/error.service';

@Component({
  selector: 'books-browsing-page',
  templateUrl: 'books-browsing.component.html',
  styles: ['']
})
export class BooksBrowsingPage {

  private seller = {
    accountName: 'King Döll',
    profilePicture: 'https://www.fupa.net/fupa/images/photo/big/marco-doell-954161_1.jpg'
  };
  items: BookPreview[] = [];
  start = 0;
  step = 20;
  end = 10;


  hideHeader = false;

  constructor(private _navCtrl: NavController, private _bookService: BookService,
    private _pictureService: PictureService, private _toastCtrlService: ToastCtrlService,
    private _errorService: ErrorService, private _ngZone: NgZone) {
  }

  ionViewDidLoad() {
    this.loadBooks();
  }

  onBookDetail(item: BookPreview) {
    this._navCtrl.push('BookDetailPage', { id: item.id }, { animate: true, animation: 'transition', direction: 'forward' });
  }

  onSearch() {
    this._navCtrl.push('SearchGenrePage', {}, { animate: true, animation: 'transition', direction: 'forward' });
  }

  doLoadMore(event: InfiniteScroll) {
    /*return new Promise((resolve) => {
      setTimeout(() => {
        for (var i = 0; i < 15; i++) {
          this.items.push(this.items[i]);
        }
        event.complete();
        resolve();
      }, 2000);
    })*/

    this.loadBooks();

    setTimeout(function () {
      event.complete();
    }, 1500);
  }

  onFavorites() {
    this._navCtrl.push('FavoritesPage', {}, { animate: true, animation: 'transition', direction: 'forward' });
  }

  loadBooks() {
    this._bookService.getBooks(this.start, this.end).subscribe(res => {
      let books: BookPreview[] = res;

      if (books.length < 1) {
        this._toastCtrlService.createToast("Keine weitern Bücher vorhanden");
        return;
      }

      books.forEach(element => {
        this.items.push(element);
      });

      this._pictureService.loadThumbnails(this.items);

      // from, to, erhöhen
      this.start = this.end;
      this.end += this.step;
    }, error => {
      this._errorService.displayErrorMessage("Fehler beim laden der Bücher");
    });
  }

  doRefresh(refresher) {
    this.initOnlyForTest();
    setTimeout(() => {
      refresher.complete();
    }, 1500);
  }

  initOnlyForTest() {
    this.items = [];
    this.start = 0;
    this.end = 10;
    this.loadBooks();
  }

  scrollBefore = 0;

  onScroll(ev: ScrollEvent) {

    if (this.scrollBefore > ev.scrollTop) {
      this._ngZone.run(() => {
        this.hideHeader = false;
      });
    }
    else {
      this._ngZone.run(() => {
        this.hideHeader = true;
      });
    }
    this.scrollBefore = ev.scrollTop;
  }
}


