import { Component } from '@angular/core';
import { NavController } from 'ionic-angular';
import { LoginService } from '../../../share/login.service';
import { ActionSheetCtrlService } from '../../../share/actionSheetCtrl.service';
import { User } from '../../../model/user';
import { Validators, FormGroup, FormBuilder } from '@angular/forms';

@Component({
    selector: 'editProfile-page',
    templateUrl: 'editProfile.component.html'
})
export class EditProfilePage {
//    user: User= <User>{displayName:"",photoURL:"",email:"",description:""};
    private profile: FormGroup;
    private photoURL: any = "";
    constructor(private _navCtrl: NavController, private _loginService: LoginService, private _actionSheetCtrlService: ActionSheetCtrlService,
                private _formBuilder: FormBuilder) {
                    this.createForm();
                }

    ionViewWillEnter() {
        let firebaseUser = this._loginService.getUser();
        if(firebaseUser) {
            this.profile.controls['name'].setValue(firebaseUser.displayName);
            this.profile.controls['mail'].setValue(firebaseUser.email);
            this.photoURL = firebaseUser.photoURL;
            this.profile.controls['description'].setValue("Todo GET Description from API");
            
        }
    }

    saveProfile() {
        this._loginService.updateUser(this.profile.controls['name'].value,this.photoURL);
        this._navCtrl.pop();
    }


    uploadPhoto(options) {
        this._actionSheetCtrlService.uploadPhoto().then(pic => {
            if (pic !== 'failed') {
                this.photoURL = pic;
                this._loginService.updateUser(undefined,this.photoURL);
            }
          }).catch(err => {
            
          });
    }

    private createForm() {
        this.profile = this._formBuilder.group({
            name: ['', [
                Validators.required
            ]
            ],
            mail: ['', [
                Validators.required
            ]
            ],
            description: ['', [
                Validators.required
            ]
            ]
        });
    }
}


