import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
    name: "hideIban"
})

export class IbanPipe implements PipeTransform {
    transform(value: string) {
        var start = value.substring(0, 4);
        var mid = '****';
        var end = value.substring(8)

        return start + mid + end;
    }
}


