import { Component } from '@angular/core';
import {  NavParams } from 'ionic-angular';
import { BookService } from '../../share/book.service';
import { BookDetail } from '../../model/bookDetail';
import { PictureService } from '../../share/picture.service';
import { ErrorService } from '../../share/error.service';

@Component({
  selector: 'book-detail-page',
  templateUrl: 'book-detail.component.html'
})
export class BookDetailPage {

  private id: string;
  private item: BookDetail = <BookDetail>{};
  detailIcon = "ios-arrow-down";
  showDetails = false;
  private seller = {
    accountName: 'King Döll',
    profilePicture: 'https://www.fupa.net/fupa/images/photo/big/marco-doell-954161_1.jpg'
  };

  private slides = [];
  constructor(public navParams: NavParams, private _bookService: BookService, private _pictureSerivce: PictureService
  ,private _errorService: ErrorService) {
    // evtl kann das noch anders geloest werden:
    this.item.authors = [""];
  }

  ionViewDidLoad() {
    this.initializeItem();
  }

  initializeItem() {
    this.id = this.navParams.get('id');
    if (this.id) {
      this._bookService.getBookDetail(this.id).subscribe(succ => {
          this.item = succ;
          this.loadPictures();
      },
        error => {
          this._errorService.displayErrorMessage("Buch konnte nicht geladen werden!");
        });
    }
  }

  loadPictures() {
    this.item.pictureIds.forEach(elem => {
      this.loadPictureById(elem);
    });
  }


  loadPictureById(id: string) {
    this._pictureSerivce.getPicture(id).subscribe(succ => {
      if (succ._body) {
        this.slides.push(succ._body);
      }
    },
      error => {
        
      });
  }

  doRefresh(refresher) {
    setTimeout(() => {
      refresher.complete();
    }, 2000);
  }

  showOrHideDetails() {
    if(this.detailIcon === "ios-arrow-down") {
      this.showDetails = true;
      this.detailIcon = "ios-arrow-up";
    } else {
      this.showDetails = false;
      this.detailIcon = "ios-arrow-down";
    }
  }
}


