import { Injectable } from '@angular/core';
import { ActionSheetController } from 'ionic-angular';
import { CameraService } from './camera.service';

@Injectable()
export class ActionSheetCtrlService {

    constructor(private _actionSheetCtrl: ActionSheetController, private _cameraService: CameraService) { }


    uploadPhoto() {
        return new Promise((resolve, reject) => {
            let actionSheet = this._actionSheetCtrl.create({
                title: 'Foto',
                buttons: [
                    {
                        text: 'Bild aufnehmen',
                        role: 'destructive',
                        handler: () => {
                            resolve(this.takePic('camera'));
                        }
                    },
                    {
                        text: 'Foto aus Album wählen',
                        handler: () => {
                            resolve(this.takePic('library'));
                        }
                    },
                    {
                        text: 'Abbrechen',
                        role: 'cancel',
                        handler:
                        () => {
                            reject("Abgebrochen");
                        }
                    }
                ]
            });
            actionSheet.present();
        });
    }

    takePic(options) :PromiseLike<string> {
        return this._cameraService.takePic(options);
    }

}


