import { Component, ViewChild, NgZone } from '@angular/core';
import { Platform, Nav, IonicPage } from 'ionic-angular';
import { SplashScreen } from '@ionic-native/splash-screen';
import { LoginService } from './share/login.service';
import { StatusBar } from '@ionic-native/status-bar';

@IonicPage({
  name: 'my-page',
  segment: 'some-path'
})
@Component({
  templateUrl: './app.html'
})
export class MyApp {
  @ViewChild(Nav) nav: Nav;
  
  public rootPage:any;

  constructor(public platform: Platform, public splashScreen: SplashScreen, private _loginService: LoginService, private _ngZone: NgZone,
  private _statusbar: StatusBar) {
    this.platform.ready().then(() => {
      this._statusbar.backgroundColorByHexString('008080');


      var unsubscribe = this._loginService.getUserForLogin().onAuthStateChanged(user => {
        if (user) {
          this.setRoot('TabsPage');
        } else {
          this.setRoot('LoginPage')
        }

        unsubscribe();

        var that = this;
        setTimeout(function() {
          that.splashScreen.hide();
        }, 100);
      }, error => {
      });
    });
  }

  private setRoot(page:any) {
    this._ngZone.run(() => {
      this.nav.setRoot(page);
    })
  }
}


